.follow {
  // .follow__title

  &__title {
    margin-bottom: clamp(8px, -1.184rem + 3.51vw, 24px);
  }

  // .follow__grid

  &__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, 1fr);
    }

    > *:nth-child(1) {
      grid-column: span 2;
      grid-row: span 2;
    }
  }

  // .follow__img

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
