.header {
  position: relative;
  z-index: 101;
  padding: clamp(8px, -0.447rem + 1.97vw, 17px) 0;
  height: auto;
  background-color: var(--white);

  // .header__container

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .header__logo

  &__logo {
  }
}

.logo {
  // .logo__link

  &__link {
  }

  // .logo__img

  &__img {
  }
}

.nav {
  margin-left: auto;

  @include small-tablet {
    display: none;
  }

  // .nav__list

  &__list {
    display: flex;
    align-items: center;
    gap: clamp(15px, -1.273rem + 4.61vw, 36px);
  }

  // .nav__item

  &__item {
  }

  // .nav__link

  &__link {
    font-size: clamp(16px, 0.705rem + 0.55vw, 18px);
    line-height: 180%;
    text-align: center;
    text-transform: capitalize;
    color: var(--neutral-gray404040);

    &:hover {
      text-decoration: underline;
      text-underline-offset: 0.3em;
    }
  }
}

.user {
  // .user__account

  &__account {
    display: flex;
    align-items: center;
    gap: clamp(8px, -1.184rem + 3.51vw, 24px);

    &--left {
      margin-right: clamp(20px, 0.659rem + 1.1vw, 24px);
      margin-left: auto;
      gap: clamp(8px, -1.184rem + 3.51vw, 24px);

      @include small-tablet {
        order: -1;
        margin-right: unset;
        margin-left: unset;
      }
    }
  }

  // .user__account-item

  &__account-item {
    svg {
      width: 24px;
      height: 24px;
    }

    // .user__account-item--person

    &--person {
      @include small-tablet {
        display: none;
      }
    }
  }

  // .user__account-button

  &__account-button {
    display: none;

    @include small-tablet {
      display: block;
    }
  }

  // .user__account-favorite

  &__account-favorite {
    fill: var(--white);
    stroke: var(--black);
    stroke-width: 2px;
  }

  // .user__account-link

  &__account-link {
    display: inline-block;
    transition: background-color 0.2s ease-in-out;

    &:hover {
      background-color: var(--neutral-grayededed);
    }
  }
}
