.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  height: 40px;
  min-width: 185px;
  font-weight: 400;
  font-size: 14px;
  line-height: 171%;
  text-align: center;
  text-transform: capitalize;
  color: var(--black);
  background-color: var(--white);
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

  &:hover {
    color: var(--white);
    background-color: var(--neutral-graycbcbcb);
  }

  @include small-tablet {
    padding: 8px 9px;
    min-width: unset;
  }

  &--wide {
    @include small-tablet {
      display: inline-block;
      padding: 8px 16px;
      width: 100%;
      text-align-last: left;
    }
  }
}
