.mobile-nav {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
  padding: 106px 20px 56px;
  width: 100%;
  height: 100%;
  background-color: var(--white);
  transform: translateY(-100%);
  transition: transform 0.3s ease-in-out;

  // .mobile-nav__btns

  &__btns {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: auto;
    border-top: 1px solid var(--neutral-graycbcbcb);
    padding-top: 14px;
    gap: 16px;
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--primary-primary-600);
    padding: 8px;
    width: 152px;
    height: 40px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 171%;
    text-align: center;
    text-transform: capitalize;
    color: var(--primary-primary-600);
    gap: 8px;
  }
}

.mobile-nav.active {
  transform: translateY(0);
}

.roll {
  margin-bottom: 84px;

  // .roll__list

  &__list {
    display: grid;
    gap: 32px;
  }

  // .roll__item

  &__item {
    border-bottom: 1px solid var(--black);
  }

  // .roll__item-btn

  &__item-btn {
    position: relative;
    width: 100%;
    text-align-last: left;

    &::after {
      content: "";
      position: absolute;
      right: 8px;
      top: 50%;
      width: 12px;
      height: 8px;
      background-image: url("../img/icons/arrow-down.svg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      transition: transform 0.3s ease-in-out;
    }
  }

  .roll__item-btn.active {
    &::after {
      transform: rotate(180deg);
    }
  }

  // .roll__item-title

  &__item-title {
    font-weight: 400;
    font-size: 14px;
    line-height: 180%;
    text-transform: capitalize;
    color: var(--black);
  }

  // .roll__item-links

  &__item-links {
    display: none;
    padding: 24px 0 16px 35px;
  }

  // .roll__item-link

  &__item-link {
    padding-top: 7px;

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    a {
      display: block;
      width: 100%;
      font-weight: 400;
      font-size: 14px;
      line-height: 180%;
      text-transform: capitalize;
      color: var(--neutral-gray404040);
    }
  }
}
