.bestseller {
  // .bestseller__header

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: clamp(8px, -1.184rem + 3.51vw, 24px);
  }

  // .bestseller__title

  &__title {
  }

  // .bestseller__button

  &__button {
    @include small-tablet {
      display: none;
    }
  }

  // .bestseller__link

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 171%;
    color: var(--primary-primary-600);

    &:hover {
      text-decoration: underline;
      text-underline-offset: 0.3em;
    }
  }
}
