.collection {
  // .collection__title

  &__title {
    margin-bottom: clamp(8px, -1.184rem + 3.51vw, 24px);
  }

  // .collection__cols

  &__cols {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(16px, 0.158rem + 1.75vw, 24px);
  }

  // .collection__col

  &__col {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: clamp(24px, -2.711rem + 8.77vw, 64px);
  }
}
