@mixin image-set($pathToImage) {
  $fileName: unquote('#{$pathToImage}');

  background-image: url('#{$fileName}.jpg'); // Fallback for non-supporting browsers
  background-image: image-set(
    url('#{$fileName}@2x.webp') 2x type('image/webp'),
    url('#{$fileName}.webp') type('image/webp'),
    url('#{$fileName}@2x.jpg') 2x type('image/jpeg'),
    url('#{$fileName}.jpg') type('image/jpeg')
  );
}

@mixin footerToBottom {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}

@mixin mr($value) {
  &:not(:last-child) {
    margin-right: $value;
  }
}

@mixin ml($value) {
  &:not(:last-child) {
    margin-left: $value;
  }
}

@mixin mb($value) {
  &:not(:last-child) {
    margin-bottom: $value;
  }
}

@mixin mt($value) {
  &:not(:last-child) {
    margin-top: $value;
  }
}

@mixin pseudo() {
  content: "";
  display: block;
}
