.top-info {
  position: relative;
  z-index: 101;
  padding: 7px 0;
  background-color: var(--primary-primary-600);

  // .top-info__container

  &__container {
    p {
      font-weight: 600;
      font-size: clamp(10px, 0.414rem + 0.44vw, 12px);
      letter-spacing: 0.07em;
      text-align: center;
      color: var(--white);

      @include small-tablet {
        font-weight: 400;
        line-height: 140%;
      }
    }
  }

  @include small-tablet {
    padding: 0;
  }
}
