.hero {
  @include image-set("../img/hero/hero");
  @include bg-cover;

  padding-bottom: clamp(16px, -8.158rem + 19.08vw, 103px);
  height: 600px;
  background-position: top center;
  background-color: var(--white);

  // .hero__container

  &__container {
    height: 100%;
  }

  // .hero__content

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    height: 100%;
    max-width: 495px;

    @include desktop {
      max-width: 168px;
    }
  }

  // .hero__title

  &__title {
    margin-bottom: 27px;
    font-weight: 500;
    font-style: italic;
    font-size: clamp(20px, -0.224rem + 3.07vw, 34px);
    line-height: 180%;

    @include small-tablet {
      margin-bottom: 32px;
      color: var(--white);
    }
  }

  // .hero__button

  &__button {}

  // .hero__btn

  &__btn {}

  @include small-tablet {
    height: 551px;
  }
}
