.card {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 392px;

  // .card__fav-button

  &__fav-button {
    position: absolute;
    right: 24px;
    top: 24px;
    z-index: 10;

    @include mobile {
      right: 8px;
      top: 8px;
    }
  }

  // .card__fav-btn

  &__fav-btn {
    transition: stroke 0.3s ease-in-out;

    &:hover {
      .user__account-favorite {
        stroke: var(--state-color-error);
      }
    }
  }

  .card__fav-btn.active {
    .user__account-favorite {
      fill: var(--state-color-error);
      stroke: var(--state-color-error);
      ;
    }
  }

  // .card__image

  &__image {}

  // .card__img

  &__img {}

  // .card__content

  &__content {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: flex-start;
    padding: 5px 8px;
    text-transform: capitalize;
    gap: 8px;

    @include mobile {
      padding-left: 0;
    }
  }

  // .card__title

  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    color: var(--black);

    a::after {
      content: '';
      position: absolute;
      inset: 0;
      width: 100%;
    }

    @include mobile {
      font-weight: 600;
      font-size: 14px;
    }
  }

  // .card__cols

  &__cols {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include mobile {
      display: grid;
      grid-template-columns: 1fr;
    }
  }

  // .card__descr

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 180%;
    white-space: nowrap;

    @include mobile {
      font-size: 14px;
      line-height: 140%;
    }
  }

  // .card__price

  &__price {
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;

    @include mobile {
      place-self: end;
      font-weight: 600;
      font-size: 14px;
    }
  }

  // .card__colors

  &__colors {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  // .card__color

  &__color {
    border-radius: 24px;
    width: 24px;
    height: 24px;

    @include mobile {
      width: 16px;
      height: 16px;
    }
  }

  .user {
    // .user__account-favorite

    &__account-favorite {
      width: 24px;
      height: 24px;
    }
  }
}
