.footer {
  margin-top: clamp(24px, -5.237rem + 14.04vw, 88px);
  height: 70px;
  background-color: #000;

  // .footer__copyright

  &__copyright {
    padding: 24px 32px;
    font-weight: 400;
    font-size: 12px;
    line-height: 180%;
    text-transform: capitalize;
    color: var(--neutral-graycbcbcb);

    @include small-tablet {
      padding: 8px 0;
    }
  }

  @include small-tablet {
    height: auto;
  }
}
